.image {
    opacity: 1;
    display: block;
    width: 100%;
    height: 200px;
    transition: .5s ease;
    backface-visibility: hidden;
}

#portfolio .card {
    width: 100%;
    height: 200px;
    margin-bottom: 20px;
}

.middle {
    transition: ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.card:hover .image {
    opacity: 0.5;
}

.card:hover .middle {
    background-color: #434343;
    width: 100%;
    height: 100%;
    opacity: 0.85;
}

.text {
    opacity: 1;
    color: white;
    font-size: auto;
    padding: 16px 32px;
}
