@import url(https://fonts.googleapis.com/css2?family=Wallpoet&display=swap" rel="stylesheet);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
	background: #121212;
	height: 60px;
	color: #fff;
	cursor: pointer;
}

.navbar > .active {
	border-bottom: 1px solid #5bc0de;
}

.navbar a {
	color: white;
	font-family: 'Wallpoet', Arial, Helvetica, serif, sans-serif;
	text-decoration: none;
	margin: 10px 5px;
}

.navbar a:hover {
	color: #5bc0de;
}
.header {
    position: relative;
    height: 100vh;
    min-height: 500px;
    width: 100%;
    background: #121212;
    background-size: cover;
    -webkit-background-size: cover;
    text-align: center;
    overflow: hidden;
}

.profilepic {
    display: inline-block;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 220px;
    height: 100%;
    position: relative;
    overflow: hidden;
    border-radius: 100%;
    border: solid 4px #fff;
}

.btn {
    padding: 20px;
    margin: 20px;
}

.btn:hover {
    padding: 20px;
    margin: 20px;
}

.social,
.fa-github,
.fa-linkedin {
    margin: 5px;
}

.social a {
    color: #fff;
}

.social a:hover {
    color: #5bc0de;
}
#about {
    position: relative;
    height: 100%;
    min-height: 500px;
    width: 100%;
    background: url(/reactPortfolio/static/media/joeking-36.faf6197e.jpg) no-repeat top center;
    background-size: cover;
    -webkit-background-size: cover;
    text-align: center;
    overflow: hidden;
}

.aboutme {
    position: relative;
    padding: 70px;
    font-size: 18px;
}

.contact a {
    color: #fff;
}
.image {
    opacity: 1;
    display: block;
    width: 100%;
    height: 200px;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

#portfolio .card {
    width: 100%;
    height: 200px;
    margin-bottom: 20px;
}

.middle {
    transition: ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.card:hover .image {
    opacity: 0.5;
}

.card:hover .middle {
    background-color: #434343;
    width: 100%;
    height: 100%;
    opacity: 0.85;
}

.text {
    opacity: 1;
    color: white;
    font-size: auto;
    padding: 16px 32px;
}

#resume {
    position: relative;
    height: 100%;
    min-height: 500px;
    width: 100%;
    background: #fff;
    background-size: cover;
    -webkit-background-size: cover;
    text-align: center;
    overflow: hidden;
}

#education {
    text-decoration: underline;
    -webkit-text-decoration-color: #5bc0de;
            text-decoration-color: #5bc0de;
    line-height: 20px;
    margin: 5px;
    padding: 10px;
}

#school {
    line-height: 30px;
}


#contact {
    position: relative;
    height: 630px;
    min-height: 500px;
    width: 100%;
    background: #121212;
    background-size: cover;
    -webkit-background-size: cover;
    text-align: center;
    overflow: hidden;
}

.form-group {
    margin-top: 20px;
}

input {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
}

textarea {
    width: 100%;
    height: 200px;
    padding: 12px 20px;
    margin: 8px 0;
}

button {
    background: #121212;
    border: none;
    color: #fff;
    padding: 15px 30px;
    border-radius: 4px;
}

button:hover {
    background: #fff;
    border: none;
    color: #121212;
    padding: 15px 30px;
}
.footer {
    background: #121212;
    background-size: cover;
    -webkit-background-size: cover;
    text-align: center;
    overflow: hidden;
}

.fa-chevron-circle-up {
    color: #fff;
    cursor: pointer;
}

.fa-chevron-circle-up:hover {
    color: #5bc0de;
}
.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

